import React, { useContext } from "react";
import styled from "styled-components";
import { bookData } from "../data/bookData";
import { ShopContext } from "../context/ShopContext";
import CartItem from "./CartItem";

const Modal = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background-color: #00000055;
  z-index: 10;
`;

const Wrapper = styled.div`
  position: fixed;
  z-index: 15;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 15px;
  margin: 0;
  background: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 15px;
  border-radius: 10px;
`;

const Title = styled.h1`
  text-align: center;
  margin-top: 20px;
  margin-bottom: 10px;
`;
const Products = styled.div`
  display: flex;
  height: 350px;
  width: 350px;
  flex-direction: column;
  align-items: center;
  gap: 10px;
  overflow-y: scroll;
`;

const Total = styled.div``;

const Checkout = styled.button`
  text-decoration: none;
  position: relative;
  color: black;
  font-size: 18px;
  font-weight: 200;
  letter-spacing: 1px;
  padding: 5px 10px 5px;
  outline: 0;
  border: 1px solid black;
  cursor: pointer;
  position: relative;
  background-color: rgba(0, 0, 0, 0);
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;

  &:hover {
    background-color: #ffe54c;
    transition: 0.9s;
  }
`;

const Close = styled.button`
  text-decoration: none;
  position: relative;
  color: black;
  font-size: 18px;
  font-weight: 200;
  letter-spacing: 1px;
  padding: 5px 10px 5px;
  outline: 0;
  border: 1px solid black;
  cursor: pointer;
  position: relative;
  background-color: rgba(0, 0, 0, 0);
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;

  &:hover {
    background-color: #ffe54c;
    transition: 0.9s;
  }
`;

const Cart = (props) => {
  const { total, cartItems, toggleCartVisibility } = useContext(ShopContext);

  return (
    <div>
      <Modal>
        <Wrapper>
          <Title>Your Book Cart</Title>
          {console.log(Object.values(cartItems).reduce((a, b) => a + b, 0))}

          {Object.values(cartItems).reduce((a, b) => a + b, 0) > 0 ? (
            <Products>
              {bookData.map((book) => {
                if (cartItems[book.id] !== 0) {
                  return <CartItem key={book.id} book={book} />;
                } else {
                  return "";
                }
              })}
            </Products>
          ) : (
            <>Cart is Empty! </>
          )}
          <Total>Total ${total}</Total>
          <Checkout>Checkout</Checkout>
          <Close onClick={toggleCartVisibility}>Close</Close>
        </Wrapper>
      </Modal>
    </div>
  );
};

export default Cart;
