import React, { useContext } from "react";
import styled from "styled-components";
import ShoppingCartIcon from "@mui/icons-material/ShoppingCart";
import { NavLink } from "react-router-dom";
import Cart from "./Cart";
import { ShopContext } from "../context/ShopContext";

const Wrapper = styled.section`
  padding: 0.1rem;
  margin: 0;
  background: #577d86;
  height: 18vh;

  @media (max-width: 400px) {
    padding: 0.2rem;
    height: auto;
  }
`;

const Title = styled.h1`
  font-size: 36px;
  text-align: center;
  color: #ffe54c;
  margin-bottom: 0.5rem;

  @media (max-width: 400px) {
    margin-bottom: 0rem;
  }
`;

const NavContainer = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: center;
  gap: 2rem;
  padding: 0.5rem;

  @media (max-width: 400px) {
    /* flex-direction: column; */
    gap: 0.4rem;
  }
`;

const NavItems = styled.button`
  text-decoration: none;
  position: relative;
  color: black;
  font-size: 18px;
  font-weight: 200;
  letter-spacing: 1px;
  padding: 5px 10px 5px;
  outline: 0;
  border: 1px solid black;
  cursor: pointer;
  position: relative;
  background-color: rgba(0, 0, 0, 0);
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;

  &:hover {
    background-color: #ffe54c;
    transition: 0.9s;
  }
`;

const ItemCounter = styled.div`
  position: absolute;
  width: 25px;
  height: 25px;
  color: white;
  background-color: red;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  bottom: 0;
  right: 0;
  transform: translate(45%, 45%);
`;

const NavBar = () => {
  const { cartItems, cartVisibility, toggleCartVisibility } =
    useContext(ShopContext);

  return (
    <div>
      <Wrapper>
        <Title>Magickal Book Store</Title>
        <NavContainer>
          <NavItems as={NavLink} to="/">
            Home
          </NavItems>
          <NavItems as={NavLink} to="/products">
            Products
          </NavItems>
          <NavItems as={NavLink} to="/contact">
            Contact
          </NavItems>
          <NavItems>
            <ShoppingCartIcon onClick={toggleCartVisibility} />
            <ItemCounter>
              {Object.values(cartItems).reduce((a, b) => a + b, 0)}
            </ItemCounter>
          </NavItems>
          {cartVisibility && <Cart />}
        </NavContainer>
      </Wrapper>
    </div>
  );
};

export default NavBar;
