import React from "react";
import styled from "styled-components";
import { NavLink } from "react-router-dom";

const Wrapper = styled.section`
  background-image: url(https://images.unsplash.com/photo-1593430980369-68efc5a5eb34?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxzZWFyY2h8M3x8Ym9va3NoZWxmfGVufDB8fDB8fA%3D%3D&auto=format&fit=crop&w=1000&q=60);
  background-size: cover;
  height: 82vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  @media (max-width: 400px) {
    padding: 0.2rem;
    height: 90vh;
  }
`;

const SmallText = styled.div`
  font-size: 1rem;
  font-weight: bold;
  color: #ffe54c;
  background-color: #577d86;
  padding: 0.5rem;
  border-radius: 15px 30px;
`;

const LargeText = styled.div`
  font-size: 2rem;
  color: #ffe54c;
  background-color: #577d86;
  padding: 0.5rem;
  border-radius: 15px 30px;
  @media (max-width: 400px) {
    font-size: 1.3rem;
  }
`;

const Button = styled.button`
  text-decoration: none;
  color: black;

  font-size: 18px;
  font-weight: 500;
  letter-spacing: 1px;
  padding: 5px 10px 5px;
  outline: 0;
  border: 1px solid black;
  cursor: pointer;
  position: relative;
  background-color: #577d86;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;

  &:hover {
    background-color: #ffe54c;
    transition: 0.9s;
  }
`;

const Home = () => {
  return (
    <div>
      <Wrapper>
        <SmallText>The best book store ever!</SmallText>
        <LargeText>Where your imagination comes alive :)</LargeText>
        <Button as={NavLink} to="/products">
          Look at our selection
        </Button>
      </Wrapper>
    </div>
  );
};

export default Home;
