import React, { useContext } from "react";
import styled from "styled-components";
import { ShopContext } from "../context/ShopContext";

const Wrapper = styled.section`
  background-color: #ffe54c;
  height: 350px;
  width: 250px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 5px;
  border-radius: 15px;
  padding: 10px 0px 20px 0px;

  &:hover {
    transition: 0.4s;
    background-color: #577d86;
  }
`;

const Image = styled.img`
  margin-top: 15px;
  width: 200px;
  height: 250px;
  border-radius: 15px;
`;

const Title = styled.div`
  font-weight: bold;
  font-size: 18px;
  text-align: center;
`;

const Price = styled.div`
  font-size: 18px;
`;

const Button = styled.button`
  text-decoration: none;
  color: black;
  background-color: #ffe54c;
  font-size: 18px;
  font-weight: 500;
  letter-spacing: 1px;
  padding: 5px 10px 5px;
  outline: 0;
  border: 1px solid black;
  cursor: pointer;
  position: relative;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;

  &:hover {
    background-color: #ffe54c;
    transition: 0.9s;
    background-color: #577d86;
  }
`;

const Card = ({ bookData }) => {
  const { addToCart, cartItems } = useContext(ShopContext);
  const cartItemAmount = cartItems[bookData.id];
  return (
    <div>
      <Wrapper>
        <Image src={bookData.imageUrl} />
        <Title>{bookData.name}</Title>
        <Price>{bookData.price}</Price>
        <Button onClick={() => addToCart(bookData.id)}>
          Add to Cart {cartItemAmount > 0 && <> ({cartItemAmount}) </>}
        </Button>
      </Wrapper>
    </div>
  );
};

export default Card;
